	components:{
		divider,
		freeAvater,
	},
	data() {
		return {
			background: {
				backgroundColor: '#fff',
			},
			keyword:"",
			checkedIndex:1,
			show:false
		}
	},
	methods: {
		switchType(index){
			if(index==1){
				
				this.checkedIndex=1
				
			}
